<template>
	<div id="main">
	  <div id="content" v-html="content"></div>
	</div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Main',
  data () {
    return {
      content: '<h2>Hello! Welcome come Blog</h2>'
    }
  },
  watch: {
    '$route' (to, from) {
      console.log(to)
      let that = this
      let url = '/static/note/' +
                to.params.folder +
                '/' +
                to.params.filename;
      // 请求 html 文件
	  axios.get(url).then(function (res) {
        console.log(res)
        that.content = res.data
      })
    }
  }
}
</script>

<style>
#main {
  padding-right: 160px;
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
}
#content {
  padding: .8rem 1.3rem;
}
#table-of-contents {
  width: 160px;
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  font-size: .6rem;
  padding-top: 60px;
  overflow-y: auto;
  border-left: 1px solid #99CCCC;
}
#table-of-contents h2 {
  font-size: .7rem;
  padding: .6rem 0 .4rem 0;
}
#text-table-of-contents {
  padding-left: .3rem;
}
#text-table-of-contents a {
  text-decoration: none;
  color: #9966CC;
  line-height: .8rem;
}
#text-table-of-contents > ul > li > ul{
  padding-left: .6rem;
}
</style>



